<template lang="pug">
#app
  #everything(class="w-screen h-screen bg-blue-200 mx-auto flex flex-col items-center justify-around")
    #topbar(class="w-full")
      #topbarstarted(
        v-if="started"
        class="text-5xl w-full flex flex-col sm:flex-row items-center sm:items-start justify-around"
      )
        p Current: <b>{{ cur }}</b>
        p Time: <b>{{ (time / 100).toFixed(2) + "s" }}</b>
      #topbarnotstarted(
        v-else
        class="text-5xl w-full flex flex-col sm:flex-row items-center sm:items-start justify-around"
      )
        p Click Sixteen
        p Last Time: <b>{{ lastTime === 0 ? "N/A" : (lastTime / 100).toFixed(2) + "s" }}</b>

    #playarea(class="m-5 flex-1 w-11/12 md:w-3/4 border border-gray-700")
      #playareastarted(v-if="started" class="h-full")
        #sixteen(v-if="currentMode === 'sixteen'" class="h-full flex flex-wrap items-stretch")
          #numberbuttons(v-for="num in numsSixteen" :key="num" class="w-1/4 h-1/4")
            button(
              @click="increment(num)"
              class="w-full h-full border-2 border-gray-700 bg-gray-300 text-6xl"
            ) {{ num }}
        #twentyfive(v-if="currentMode === 'twentyfive'" class="h-full flex flex-wrap items-stretch")
          #numberbuttons(v-for="num in numsTwentyFive" :key="num" class="w-1/5 h-1/5")
            button(
              @click="increment(num)"
              class="w-full h-full border-2 border-gray-700 bg-gray-300 text-6xl"
            ) {{ num }}
      #playareanotstarted(v-else class="w-full h-full")
        #optionsbuttons(class="flex flex-col w-full h-full")
          button(
            @click="startSixteen"
            class="w-full h-full border border-gray-700 bg-gray-400"
          )
            p(class="text-6xl") START 16
            p(class="text-2xl text-gray-700") Click all the numbers from 1 to 16
          button(
            @click="startTwentyFive"
            class="w-full h-full border border-gray-700 bg-gray-400"
          )
            p(class="text-6xl") START 25
            p(class="text-2xl text-gray-700") Click all the numbers from 1 to 25
</template>

<script>
export default {
  name: "Game",
  data: () => {
    return {
      numsSixteen: [],
      numsTwentyFive: [],
      currentMode: "",
      started: false,
      cur: 1,
      time: 0,
      timer: null,
      lastTime: 0
    };
  },
  created() {
    const numsSixteen = [];
    for (let i = 1; i <= 16; i++) {
      numsSixteen.push(i);
    }
    this.randomize(numsSixteen);
    this.numsSixteen = numsSixteen;

    const numsTwentyFive = [];
    for (let i = 1; i <= 25; i++) {
      numsTwentyFive.push(i);
    }
    this.randomize(numsTwentyFive);
    this.numsTwentyFive = numsTwentyFive;
  },
  methods: {
    startSixteen() {
      this.currentMode = "sixteen";
      this.start();
    },
    startTwentyFive() {
      this.currentMode = "twentyfive";
      this.start();
    },
    start() {
      this.started = true;

      this.timer = setInterval(() => {
        this.time += 1;
      }, 10);
    },
    increment(num) {
      if (num === this.cur) {
        this.cur += 1;
      }
      if (
        (this.currentMode === "sixteen" && this.cur === 17) ||
        (this.currentMode === "twentyfive" && this.cur === 26)
      ) {
        clearTimeout(this.timer);
        this.started = false;
        this.lastTime = this.time;
        this.time = 0;
        this.cur = 1;
        this.randomize(this.numsSixteen);
        this.randomize(this.numsTwentyFive); // this is ugly, i should only randomize one
      }
    },
    randomize(arr) {
      for (let i = arr.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        const temp = arr[i];
        arr[i] = arr[j];
        arr[j] = temp;
      }
    }
  }
};
</script>
